.custom-checkbox {
  width: 20px;
  height: 20px;
  cursor: pointer;
  position: relative;
}
.custom-checkbox.-small {
  width: 15px;
  height: 15px;
}
.custom-checkbox.-small > .indicator {
  width: 15px;
  height: 15px;
}
.custom-checkbox.-small > .indicator:after {
  top: 3px;
  left: 6px;
  width: 3px;
  height: 8px;
}
.custom-checkbox:hover input ~ .indicator {
  background: #2f477a;
}
.custom-checkbox:hover input:checked ~ .indicator {
  background: #2f477a;
}
.custom-checkbox input {
  position: absolute;
  z-index: -1;
  opacity: 0;
}
.custom-checkbox input:checked ~ .indicator:after {
  display: block;
}
.custom-checkbox input:disabled ~ .indicator {
  pointer-events: none;
  opacity: 0.6;
}
.custom-checkbox > .indicator {
  position: absolute;
  top: 2px;
  left: 0;
  width: 20px;
  height: 20px;
  background: #e6e6e6;
}
.custom-checkbox > .indicator:after {
  content: '';
  top: 3px;
  left: 8px;
  width: 5px;
  height: 11px;
  transform: rotate(45deg);
  border: solid #d9534f;
  border-width: 0 2px 2px 0;
  display: none;
  position: absolute;
}
